@import 'base.scss';

html, body {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(180deg, #6aa8f8 0%, #cdf2fd 100%);
}

@keyframes yanhua {
  0%{
    transform :scale(0);
    opacity :1;
  }
  70%{
    transform :scale(1);
    opacity :1;
  }
  100%{
    transform :scale(1);
    opacity :0;
  }
}

@keyframes moves22 {


  0% {
    -webkit-transform: rotate(0deg);
  }

  25% {
    -webkit-transform: rotate(90deg);
  }

  50% {
    -webkit-transform: rotate(180deg);
  }

  75% {
    -webkit-transform: rotate(270deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes moves1 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes moves13 {
  0% {
    bottom: 10vh;
  }
  25% {
    bottom: 11vh;
  }
  50% {
    bottom: 10vh;
  }

  75% {
    bottom: 11vh;
  }
  100% {
    bottom: 10vh;
  }
}

@keyframes moves2 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes moves3 {
  0% {
    transform: scale(0.9);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.9);
  }
}

@keyframes pos1 {
  0% {
    left: -20vw;
  }

  100% {
    left: 1vw;
  }
}
@keyframes pos11 {
  0% {
      left: -20vw;
  }

  100% {
      left: 1vw;
  }
}

@keyframes pos112 {
  0% {
      opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes pos2 {
  0% {
    right: -37vw;
  }

  100% {
    right: 1vw;
  }
}
@keyframes pos22 {
  0% {
      right: -37vw;
  }

  100% {
      right: 1vw;
  }
}

@keyframes pos3 {
  0% {
    top: 120vh;
  }

  100% {
    top: 20vh;
  }
}

@keyframes pos31 {
  0% {
    top: 120vh;
  }

  100% {
    top: 23vh;
  }
}
@keyframes loadingwidth {
  0% {
        width: 0%;
  }

  100% {
    width: 100%;
  }
}
@keyframes pos32 {
  0% {
      top: 120vh;
  }

  100% {
      top: 20vh;
  }
}
@keyframes pos33 {
  0% {
        top: 120vh;
  }

  100% {
        top: 23vh;
  }
}
@keyframes fly {
  0% {
    top: -120vh;
  }

  100% {
    top: 0;
  }
}
@keyframes moves4 {
  0% {
    transform: scale(0.9);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.9);
  }
}

@keyframes guodumove {

  0% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }

  80% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

.showvideo-enter-active, .showvideo-leave-active {
  transition: all 1s ease-out;
}

.showvideo-enter, .showvideo-leave-to {
  opacity: 0;
}

.showsel-enter-active, .showsel-leave-active {
  transition: all 2s ease-out;
}

.showsel-enter, .showsel-leave-to {
  opacity: 0;
}

.showsel2-enter-active, .showsel2-leave-active {
  transition: all 2s ease-out;
}

.showsel2-enter, .showsel2-leave-to {
  opacity: 0;
}

.guodu-enter-active, .guodu-leave-active {
  transition: all 2s ease;
}

.guodu-enter, .guodu-leave-to {
  opacity: 0;
}

@keyframes trans1{
  0%{
    transform :rotate(0);
  }

  100%{
    transform :rotate(-30deg);
  }
}

